import React, { PureComponent } from 'react'
import Button from './Button'

export class Games extends PureComponent {
  render() {
    return (
      <>
        <div id='games'>

            <p className='title'>
                GRY
            </p>
           
            <div id='gamesBtn'>
                <Button name = 'Audaces Remastered' url = 'https://szymekcendecki.github.io/Audaces-remastered/' />
                <Button name = 'Audaces Powerfull' url = 'https://szymekcendecki.github.io/Audaces-powerfull/' />
                <Button name = 'Piekło z gwiazd' url = 'https://szymekcendecki.github.io/Pieklo-z-gwiazd/' />
                <Button name = 'Piracki Rejs' url = 'https://szymekcendecki.github.io/piracki-rejs/' />
                <Button name = 'Simply Game' url = 'https://szymekcendecki.github.io/simpleGame/' />
                <Button name = 'Simply Game 2' url = 'https://szymekcendecki.github.io/simpleGame2/' />
            </div>

            <p id='gamesDescription'>
              Uwzględniono tylko grywalne wersje gier.
            </p>
        </div>        
      </>
    )
  }
}

export default Games