import React, { PureComponent } from 'react'
import Button from './Button'

export class Programs extends PureComponent {

  render() {
    return (
      <>
        <div id='programs'>

          <p className='title'>
            Programy
          </p>
           
            <div id='programsBtn'>
                <Button name = 'Konwerter' url='https://szymekcendecki.github.io/simple-converter/' />
                <Button name = 'Konwerter 2' url='https://szymekcendecki.github.io/simple-converter-ver2/' />
                <Button name = 'SCSS lib.' url='https://szymekcendecki.github.io/scss_css-library/' />
                <Button name = "Atlas '85" url='https://szymekcendecki.github.io/atlas-85/'/>
            </div>

            <p id='programsDescription'>
              Programy użytkowe.
            </p>
        </div>        
      </>
    )
  }
}

export default Programs