import Header from './components/Header'
import Nav from './components/Nav'
import MainContainer from './components/MainContainer'
import Footer from './components/Footer'
import './App.css';

import React, { PureComponent } from 'react'

const aboutDescriptionEN = ""

const txtPL = {
  lang: 'EN',
  aboutTitle: 'O NAS',
  aboutDescription: 'Nazwa studia wzięła się od pewnej historii, z próbą wynajęcia pokoju w Warszawie. Krótko rzecz ujmując, założycielowi studia pokój nie został wynajęty, gdyż... problemem był ateizm owego założyciela, a to było problematyczne dla osoby chcącej wynająć pokój (historia jak najbardziej autentyczna). Data założenia tego nieoficjalnego studia to... No właśnie, z tym jest drobny kłopot, gdyż delikatnie zaginęła ta data w mrokach historii. Dla uproszczenia trzeba będzie założyć, iż oficjalnie powstało tego samego co utworzenie fanpage na fejsie (13 czerwca 2016). A nieoficjalnie to musiało być to trochę wcześniej (tak się domyślam po pierwszych postach). Od początku w planach studia było tworzenie gier pod przeglądarkę oraz nauka programowania (html, js, css). 25 stycznia studio rozpoczęło także streamowanie gier. Na pierwszy ogień poszły trzy gry: Eve Online, Neverwinter Nights oraz World of Tanks. Grafik streamów w zakładce GRAFIK.'
}


const txtEN = {
  lang: 'PL',
  aboutTitle: 'ABOUT US',
  aboutDescription: "The name of the studio comes from a story about an attempt to rent a room in Warsaw. Briefly speaking, the room was not rented to the founder of the studio, because ... the atheism of that founder was a problem, and it was problematic for a person who wanted to rent a room (the most authentic story). The date of the foundation of this unofficial studio is ... Exactly, there is a slight problem with that, as this date has been slightly lost in the darkness of history. For the sake of simplicity, it will be necessary to assume that officially the same as the creation of a fanpage on Facebook was created (June 13, 2016). And unofficially, it had to be a little earlier (I guess after the first posts). From the very beginning, the studio's plans were to create browser games and learn programming (html, js, css). On January 25, the studio also began streaming games. Three games went first: Eve Online, Neverwinter Nights and World of Tanks. The stream schedule in the GRAFIK tab."
}

export class App extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
        all: txtPL
    };

    this.changeLang = this.changeLang.bind(this)
}


changeLang(lang){
  if(this.state.all.lang === 'EN'){
    this.setState({
      all: txtEN
    });
  }else{
    this.setState({
      all: txtPL
    });
   
  }
}
 render() {
   return (
      <>
        <Header />
        <Nav changeLang = {this.changeLang} lang={this.state.all.lang}/>
        <MainContainer all={this.state.all}/>
        <Footer />
      </>
    )
  }
}

export default App


